"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/app/auth-context"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ArrowLeft, Plus, Pencil, Trash2, Search } from "lucide-react"
import Swal from "sweetalert2"

interface Site {
  id: number
  site_code: string
  site: string
  created_at?: string
  updated_at?: string
}

export default function SitesPage() {
  const router = useRouter()
  const { user, logout } = useAuth()
  const [sites, setSites] = useState<Site[]>([])
  const [filteredSites, setFilteredSites] = useState<Site[]>([])
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState("")

  useEffect(() => {
    if (!user) return
    
    if (user.role !== 'admin') {
      router.push('/dashboard')
      return
    }
    fetchSites()
  }, [user])

  useEffect(() => {
    if (searchTerm) {
      const filtered = sites.filter(site => 
        site.site_code.toLowerCase().includes(searchTerm.toLowerCase()) ||
        site.site.toLowerCase().includes(searchTerm.toLowerCase())
      )
      setFilteredSites(filtered)
    } else {
      setFilteredSites(sites)
    }
  }, [searchTerm, sites])

  const fetchSites = async () => {
    try {
      const response = await fetch("/api/sites")
      const data = await response.json()
      if (data.success) {
        setSites(data.data)
        setFilteredSites(data.data)
      }
    } catch (error) {
      console.error("Error fetching sites:", error)
      Swal.fire({
        title: "เกิดข้อผิดพลาด",
        text: "ไม่สามารถโหลดข้อมูลสาขาได้",
        icon: "error"
      })
    } finally {
      setLoading(false)
    }
  }

  const handleAddSite = async () => {
    const { value: formValues } = await Swal.fire({
      title: "เพิ่มสาขาใหม่",
      html: `
        <div style="text-align: left; margin: 20px 0;">
          <div style="margin-bottom: 15px;">
            <label style="display: block; margin-bottom: 5px; font-weight: 500;">รหัสสาขา *</label>
            <input id="site_code" class="swal2-input" placeholder="เช่น Y11" style="width: 100%; margin: 0;">
          </div>
          <div style="margin-bottom: 15px;">
            <label style="display: block; margin-bottom: 5px; font-weight: 500;">ชื่อสาขา *</label>
            <input id="site_name" class="swal2-input" placeholder="เช่น ทรัพยากรบุคคลย้ำ" style="width: 100%; margin: 0;">
          </div>
        </div>
      `,
      focusConfirm: false,
      showCancelButton: true,
      confirmButtonText: "เพิ่ม",
      cancelButtonText: "ยกเลิก",
      confirmButtonColor: "#3085d6",
      preConfirm: () => {
        const site_code = (document.getElementById("site_code") as HTMLInputElement).value
        const site_name = (document.getElementById("site_name") as HTMLInputElement).value

        if (!site_code || !site_name) {
          Swal.showValidationMessage("กรุณากรอกข้อมูลให้ครบถ้วน")
          return false
        }

        return { site_code, site_name }
      }
    })

    if (formValues) {
      try {
        const response = await fetch("/api/sites", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            site_code: formValues.site_code,
            site: formValues.site_name
          })
        })

        const data = await response.json()

        if (data.success) {
          Swal.fire({
            title: "สำเร็จ!",
            text: "เพิ่มสาขาเรียบร้อยแล้ว",
            icon: "success"
          })
          fetchSites()
        } else {
          Swal.fire({
            title: "เกิดข้อผิดพลาด",
            text: data.error || "ไม่สามารถเพิ่มสาขาได้",
            icon: "error"
          })
        }
      } catch (error) {
        Swal.fire({
          title: "เกิดข้อผิดพลาด",
          text: "ไม่สามารถเพิ่มสาขาได้",
          icon: "error"
        })
      }
    }
  }

  const handleEditSite = async (site: Site) => {
    const { value: formValues } = await Swal.fire({
      title: "แก้ไขสาขา",
      html: `
        <div style="text-align: left; margin: 20px 0;">
          <div style="margin-bottom: 15px;">
            <label style="display: block; margin-bottom: 5px; font-weight: 500;">รหัสสาขา *</label>
            <input id="site_code" class="swal2-input" value="${site.site_code}" style="width: 100%; margin: 0;">
          </div>
          <div style="margin-bottom: 15px;">
            <label style="display: block; margin-bottom: 5px; font-weight: 500;">ชื่อสาขา *</label>
            <input id="site_name" class="swal2-input" value="${site.site}" style="width: 100%; margin: 0;">
          </div>
        </div>
      `,
      focusConfirm: false,
      showCancelButton: true,
      confirmButtonText: "บันทึก",
      cancelButtonText: "ยกเลิก",
      confirmButtonColor: "#3085d6",
      preConfirm: () => {
        const site_code = (document.getElementById("site_code") as HTMLInputElement).value
        const site_name = (document.getElementById("site_name") as HTMLInputElement).value

        if (!site_code || !site_name) {
          Swal.showValidationMessage("กรุณากรอกข้อมูลให้ครบถ้วน")
          return false
        }

        return { site_code, site_name }
      }
    })

    if (formValues) {
      try {
        const response = await fetch(`/api/sites/${site.site_code}`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            site_code: formValues.site_code,
            site: formValues.site_name
          })
        })

        const data = await response.json()

        if (data.success) {
          Swal.fire({
            title: "สำเร็จ!",
            text: "แก้ไขสาขาเรียบร้อยแล้ว",
            icon: "success"
          })
          fetchSites()
        } else {
          Swal.fire({
            title: "เกิดข้อผิดพลาด",
            text: data.error || "ไม่สามารถแก้ไขสาขาได้",
            icon: "error"
          })
        }
      } catch (error) {
        Swal.fire({
          title: "เกิดข้อผิดพลาด",
          text: "ไม่สามารถแก้ไขสาขาได้",
          icon: "error"
        })
      }
    }
  }

  const handleDeleteSite = async (site: Site) => {
    const result = await Swal.fire({
      title: "ยืนยันการลบ",
      html: `คุณต้องการลบสาขา <strong>${site.site_code} - ${site.site}</strong> ใช่หรือไม่?`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#d33",
      cancelButtonColor: "#3085d6",
      confirmButtonText: "ลบ",
      cancelButtonText: "ยกเลิก"
    })

    if (result.isConfirmed) {
      try {
        const response = await fetch(`/api/sites/${site.site_code}`, {
          method: "DELETE"
        })

        const data = await response.json()

        if (data.success) {
          Swal.fire({
            title: "สำเร็จ!",
            text: "ลบสาขาเรียบร้อยแล้ว",
            icon: "success"
          })
          fetchSites()
        } else {
          Swal.fire({
            title: "เกิดข้อผิดพลาด",
            text: data.error || "ไม่สามารถลบสาขาได้",
            icon: "error"
          })
        }
      } catch (error) {
        Swal.fire({
          title: "เกิดข้อผิดพลาด",
          text: "ไม่สามารถลบสาขาได้",
          icon: "error"
        })
      }
    }
  }

  const handleLogout = () => {
    logout()
    router.push("/")
  }

  if (!user) {
    return null
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900 mx-auto"></div>
          <p className="mt-4 text-gray-600">กำลังโหลดข้อมูล...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header Navbar */}
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <div>
              <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
              <p className="text-sm text-muted-foreground">จัดการสาขา</p>
            </div>
          </div>
          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="font-medium text-sm">{user?.name || user?.username}</p>
              <p className="text-xs text-muted-foreground capitalize">{user?.role}</p>
            </div>
            <Button variant="outline" onClick={handleLogout}>
              Logout
            </Button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto p-6">
        {/* Page Header */}
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">ระบบบริหารจัดการสาขา</h1>
            <p className="text-sm text-gray-600 mt-1">
              จัดการข้อมูลสาขาในองค์กร - ทั้งหมด {sites.length} สาขา
            </p>
          </div>
          <Button onClick={handleAddSite} className="gap-2">
            <Plus className="h-4 w-4" />
            เพิ่มสาขา
          </Button>
        </div>

        {/* Search */}
        <div className="bg-white rounded-lg shadow-sm p-4 mb-6">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
            <Input
              placeholder="ค้นหา รหัสสาขา, ชื่อสาขา..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10"
            />
          </div>
        </div>

        {/* Table */}
        <div className="bg-white rounded-lg shadow-sm overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    รหัสสาขา
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    ชื่อสาขา
                  </th>
                  <th className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredSites.length === 0 ? (
                  <tr>
                    <td colSpan={3} className="px-6 py-8 text-center text-gray-500">
                      {searchTerm ? "ไม่พบข้อมูลที่ค้นหา" : "ไม่มีข้อมูลสาขา"}
                    </td>
                  </tr>
                ) : (
                  filteredSites.map((site) => (
                    <tr key={site.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm font-medium text-gray-900">
                          {site.site_code}
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <div className="text-sm text-gray-900">{site.site}</div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-center">
                        <div className="flex justify-center gap-2">
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => handleEditSite(site)}
                            className="hover:bg-blue-50 hover:text-blue-600"
                          >
                            <Pencil className="h-4 w-4" />
                          </Button>
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => handleDeleteSite(site)}
                            className="hover:bg-red-50 hover:text-red-600"
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  )
}
